<template>
  <v-container fluid fill-height style="padding:0;" id="dom" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="getDom">获取dom</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <table>
                    <colgroup>
                        <col span="2">
                        <col span="1" style="min-width:200px;">
                        <col span="1" style="min-width:200px;">
                        <col span="1" style="min-width:150px;">
                        <col span="1" style="min-width: 150px;">
                    </colgroup>

                    <tr>
                        <td></td>
                        <th>属性/方法名</th>
                        <th>测试数据</th>
                        <th>示例</th>
                        <th>输出</th>
                        <th>说明</th>
                    </tr>

                    <tr>
                        <th>1</th>
                        <td>getElementById</td>
                        <td rowspan="22">
                            <pre class="code">{{pres[0]}}</pre>
                        </td>
                        <td>document.getElementById("box");</td>
                        <td>
                            <pre class="code">{{pres[1]}}</pre>
                        </td>
                        <td>
                            <div style="min-width: 150px;">根据id获取元素</div>
                        </td>
                    </tr>

                    <tr>
                        <th>2</th>
                        <td>getElementsByClassName</td>
                        <td>document.getElementsByClassName('paragraph');</td>
                        <td>
                            <pre class="code">HTMLCollection(2) [p.paragraph, p.paragraph]</pre>
                        </td>
                        <td>根据class获取元素</td>
                    </tr>

                    <tr>
                        <th>3</th>
                        <td>getElementsByName</td>
                        <td>document.getElementsByName('mypar');</td>
                        <td>
                            <pre class="code">NodeList [p.paragraph]</pre>
                        </td>
                        <td>根据name属性值获取元素</td>
                    </tr>

                    <tr>
                        <th>4</th>
                        <td>getElementsByTagName</td>
                        <td>document.getElementsByTagName('div');</td>
                        <td>
                            <pre class="code">HTMLCollection [div#box]</pre>
                        </td>
                        <td>根据标签名获取元素</td>
                    </tr>

                    <tr>
                        <th rowspan="4">5</th>
                        <td rowspan="4">querySelector</td>
                        <td>document.querySelector('#box .paragraph');</td>
                        <td>
                            <pre class="code">{{pres[2]}}</pre>
                        </td>
                        <td>根据选择器获取匹配成功的第一个元素</td>
                    </tr>

                    <tr>
                        <td>document.querySelector('#box, .paragraph');</td>
                        <td>
                            <pre class="code">{{pres[3]}}</pre>
                        </td>
                        <td>逗号分隔的选择器表示 “或” 的意思</td>
                    </tr>

                    <tr>
                        <td>
                            <pre class="code">{{pres[4]}}</pre>
                        </td>
                        <td>
                            <pre class="code">{{pres[2]}}</pre>
                        </td>
                        <td>通过元素调用此方法，传入的选择器默认不看层级关系，只看最后一个选择器</td>
                    </tr>

                    <tr>
                        <td>
                            <pre class="code">{{pres[5]}}</pre>
                        </td>
                        <td>
                            <pre class="code">null</pre>
                        </td>
                        <td>伪类选择器:scope可以表示层级关系</td>
                    </tr>

                    <tr>
                        <th>6</th>
                        <td>querySelectorAll</td>
                        <td>
                            document.querySelectorAll('div');
                        </td>
                        <td>
                            <pre class="code">NodeList [div#box]</pre>
                        </td>
                        <td>获取所有匹配的节点</td>
                    </tr>

                    <tr>
                        <th>7</th>
                        <td>firstChild</td>
                        <td>
                            box.firstChild;
                        </td>
                        <td>
                            #text
                        </td>
                        <td>获取第一个孩子节点</td>
                    </tr>

                    <tr>
                        <th>8</th>
                        <td>lastChild</td>
                        <td>
                            box.lastChild;
                        </td>
                        <td>
                            #text
                        </td>
                        <td>获取最后一个孩子节点</td>
                    </tr>

                    <tr>
                        <th>9</th>
                        <td>firstElementChild</td>
                        <td>
                            box.firstElementChild;
                        </td>
                        <td>
                            <pre class="code">{{pres[2]}}</pre>
                        </td>
                        <td>获取第一个孩子元素节点</td>
                    </tr>

                    <tr>
                        <th>10</th>
                        <td>lastElementChild</td>
                        <td>
                            box.lastElementChild;
                        </td>
                        <td>
                            <pre class="code">{{pres[6]}}</pre>
                        </td>
                        <td>获取最后一个孩子元素节点</td>
                    </tr>

                    <tr>
                        <th>11</th>
                        <td>previousSibling</td>
                        <td>
                            box.previousSibling;
                        </td>
                        <td>
                            #text
                        </td>
                        <td>获取上一个兄弟节点</td>
                    </tr>

                    <tr>
                        <th>12</th>
                        <td>nextSibling</td>
                        <td>
                            box.nextSibling;
                        </td>
                        <td>
                            #text
                        </td>
                        <td>获取下一个兄弟节点</td>
                    </tr>

                    <tr>
                        <th>13</th>
                        <td>previousElementSibling</td>
                        <td>
                            box.previousElementSibling;
                        </td>
                        <td>
                            null
                        </td>
                        <td>获取上一个兄弟元素节点</td>
                    </tr>

                    <tr>
                        <th>14</th>
                        <td>nextElementSibling</td>
                        <td>
                            box.nextElementSibling;
                        </td>
                        <td>
                            null
                        </td>
                        <td>获取下一个兄弟元素节点</td>
                    </tr>

                    <tr>
                        <th>15</th>
                        <td>parentElement</td>
                        <td>
                            box.parentElement;
                        </td>
                        <td>
                            <pre class="code">{{pres[7]}}</pre>
                        </td>
                        <td>获取父元素节点，效果和<code>box.parentNode</code>一致</td>
                    </tr>

                    <tr>
                        <th>16</th>
                        <td>closest</td>
                        <td>
                            <pre class="code">{{pres[8]}}</pre>
                        </td>
                        <td>
                            <pre class="code">{{pres[1]}}</pre>
                        </td>
                        <td>根据传入的选择器找最接近的祖先节点或者它自己</td>
                    </tr>

                    <tr>
                        <th>17</th>
                        <td>childNodes</td>
                        <td>
                            box.childNodes;
                        </td>
                        <td>
                            NodeList(5)
                        </td>
                        <td>获取所有子节点</td>
                    </tr>

                    <tr>
                        <th>19</th>
                        <td>children</td>
                        <td>
                            box.children;
                        </td>
                        <td>
                            HTMLCollection(2)
                        </td>
                        <td>获取所有子元素节点</td>
                    </tr>

                    <tr>
                        <th>20</th>
                        <td>textContent</td>
                        <td>
                           box.textContent
                        </td>
                        <td>
                            <pre class="code">{{pres[9]}}</pre>
                        </td>
                        <td>获取所有节点的文本值，innerText会导致页面回流重绘</td>
                    </tr>

                  </table>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

         <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="domFnc">dom的增删改</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <table>
                        <colgroup>
                            <col span="2">
                            <col span="1" style="min-width:200px;">
                            <col span="1" style="min-width:200px;">
                            <col span="1" style="min-width:150px;">
                            <col span="1" style="min-width: 150px;">
                        </colgroup>

                        <tr>
                            <td></td>
                            <th>属性/方法名</th>
                            <th>原始dom树</th>
                            <th>示例</th>
                            <th>插入后的dom树</th>
                            <th>说明</th>
                            <th>备注</th>
                        </tr>

                        <tr>
                            <th>1</th>
                            <td>after</td>
                            <td rowspan="15">
                                <pre class="code">{{pres[10]}}</pre>
                            </td>
                            <td>box.after(document.createElement('p'), '文字');</td>
                            <td>
                                <pre class="code">{{pres[11]}}</pre>
                            </td>
                            <td>
                                <div style="min-width: 150px;">在指定节点后插入传入的节点</div>
                            </td>
                            <td rowspan="9"></td>
                        </tr>

                        <tr>
                            <th>2</th>
                            <td>before</td>
                            <td>box.before(document.createElement('p'), '文字');</td>
                            <td>
                                <pre class="code">{{pres[12]}}</pre>
                            </td>
                            <td>
                                <div>在指定节点前插入传入的节点</div>
                            </td>
                        </tr>

                        <tr>
                            <th>3</th>
                            <td>remove</td>
                            <td>document.querySelector('p').remove();</td>
                            <td>
                                <pre class="code">{{pres[13]}}</pre>
                            </td>
                            <td>
                                <div>删除节点</div>
                            </td>
                        </tr>

                        <tr>
                            <th>4</th>
                            <td>replaceWith</td>
                            <td>document.querySelector('p').replaceWith("a", "b");</td>
                            <td>
                                <pre class="code">{{pres[14]}}</pre>
                            </td>
                            <td>
                                <div>替换节点</div>
                            </td>
                        </tr>

                        <tr>
                            <th>5</th>
                            <td>append</td>
                            <td>box.append("a", "b");</td>
                            <td>
                                <pre class="code">{{pres[15]}}</pre>
                            </td>
                            <td>
                                <div>在节点末尾追加元素</div>
                            </td>
                        </tr>

                        <tr>
                            <th>6</th>
                            <td>prepend</td>
                            <td>box.prepend("a", "b");</td>
                            <td>
                                <pre class="code">{{pres[16]}}</pre>
                            </td>
                            <td>
                                <div>在节点开头添加元素</div>
                            </td>
                        </tr>

                        <tr>
                            <th>7</th>
                            <td>appendChild</td>
                            <td>
                                <pre class="code">{{pres[17]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[18]}}</pre>
                            </td>
                            <td>
                                <div>将元素追加到末尾，如果是页面上已存在的节点会剪切过去</div>
                            </td>
                        </tr>

                        <tr>
                            <th>8</th>
                            <td>removeChild</td>
                            <td>
                                <pre class="code">{{pres[19]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[20]}}</pre>
                            </td>
                            <td>
                                <div>删除传入的子节点</div>
                            </td>
                        </tr>

                        <tr>
                            <th>9</th>
                            <td>replaceChild</td>
                            <td>
                                <pre class="code">{{pres[21]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[22]}}</pre>
                            </td>
                            <td>
                                <div>将指定的子节点替换成其他节点</div>
                            </td>
                        </tr>

                        <tr>
                            <th>10</th>
                            <td>insertAdjacentElement</td>
                            <td>
                                <pre class="code">{{pres[23]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[24]}}</pre>
                            </td>
                            <td>
                                <div>插入节点</div>
                            </td>
                            <td rowspan="3">
                                <ul>
                                    <li>beforebegin：在当前元素前插入</li>
                                    <li>afterend：在当前元素后插入</li>
                                    <li>afterbegin：在子节点开头追加</li>
                                    <li>beforeend：在子节点后追加</li>
                                </ul>
                            </td>
                        </tr>

                        <tr>
                            <th>11</th>
                            <td>insertAdjacentHTML</td>
                            <td>
                                <pre class="code">{{pres[25]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[26]}}</pre>
                            </td>
                            <td>
                                <div>插入html片段</div>
                            </td>
                        </tr>

                        <tr>
                            <th>12</th>
                            <td>insertAdjacentText</td>
                            <td>
                                <pre class="code">{{pres[27]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[28]}}</pre>
                            </td>
                            <td>
                                <div>插入文本</div>
                            </td>
                        </tr>

                        <tr>
                            <th rowspan="3">13</th>
                            <td rowspan="3">insertBefore</td>
                            <td>
                                <pre class="code">{{pres[29]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[30]}}</pre>
                            </td>
                            <td>
                                <div>将新的节点插在传入的子节点之前</div>
                            </td>
                            <td rowspan="3"></td>
                        </tr>

                        <tr>
                            <td>
                                <pre class="code">{{pres[31]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[32]}}</pre>
                            </td>
                            <td>
                                <div>将新的节点插在传入的子节点之后</div>
                            </td>
                        </tr>

                        <tr>
                            <td>
                                <pre class="code">{{pres[33]}}</pre>
                            </td>
                            <td>
                                <pre class="code">{{pres[34]}}</pre>
                            </td>
                            <td>
                                <div>将新的节点插在最后</div>
                            </td>
                        </tr>
                    </table>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="otherDom">dom的其他常用属性和方法</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <table>
                        <colgroup>
                            <col span="2">
                            <col span="1" style="min-width:200px;">
                            <col span="1" style="min-width:200px;">
                        </colgroup>

                        <tr>
                            <td></td>
                            <th>属性/方法名</th>
                            <th>示例</th>
                            <th>说明</th>
                        </tr>

                        <tr>
                            <th>1</th>
                            <td>matches</td>
                            <td>box.matches('#box')</td>
                            <td>
                                <div style="min-width: 150px;">检查元素能否被指定选择器选中</div>
                            </td>
                        </tr>

                        <tr>
                            <th>2</th>
                            <td>isConnected</td>
                            <td>box.isConnected;</td>
                            <td>判断节点是否放到document节点或者影子（Shadow）节点中</td>
                        </tr>

                        <tr>
                            <th>3</th>
                            <td>contains</td>
                            <td>document.body.contains(box);</td>
                            <td>判断父节点是否包含传入的节点</td>
                        </tr>

                        <tr>
                            <th>4</th>
                            <td>hasChildNodes</td>
                            <td>box.hasChildNodes();</td>
                            <td>判断是否有子节点</td>
                        </tr>

                        <tr>
                            <th>5</th>
                            <td>isEqualNode</td>
                            <td>box.isEqualNode(div1);</td>
                            <td>判断两个节点是否一样</td>
                        </tr>

                        <tr>
                            <th>6</th>
                            <td>isSameNode</td>
                            <td>box.isSameNode(div1);</td>
                            <td>判断两个节点的引用是否一样</td>
                        </tr>

                        <tr>
                            <th>7</th>
                            <td>nodeName</td>
                            <td>box.nodeName;</td>
                            <td>获取节点的名称，也就是大写的标签名</td>
                        </tr>

                        <tr>
                            <th>8</th>
                            <td>nodeType</td>
                            <td>box.nodeType;</td>
                            <td>获取节点类型，常用值如下
                                <ul>
                                    <li>Node.ELEMENT_NODE：元素节点</li>
                                    <li>Node.TEXT_NODE：文本节点</li>
                                    <li>Node.COMMENT_NODE：注释节点</li>
                                    <li>Node.DOCUMENT_NODE：document节点</li>
                                </ul>
                            </td>
                        </tr>

                        <tr>
                            <th rowspan="2">9</th>
                            <td rowspan="2">cloneNode</td>
                            <td>box.cloneNode();</td>
                            <td>浅克隆节点</td>
                        </tr>

                        <tr>
                            <td>box.cloneNode(true)</td>
                            <td>深克隆节点</td>
                        </tr>

                    </table>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

           <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="domAttr">dom属性的操作</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <table>
                        <colgroup>
                            <col span="2">
                            <col span="1" style="min-width:200px;">
                            <col span="1" style="min-width:200px;">
                        </colgroup>

                        <tr>
                            <td></td>
                            <th>属性/方法名</th>
                            <th>示例</th>
                            <th>说明</th>
                        </tr>

                        <tr>
                            <th>1</th>
                            <td>getAttribute</td>
                            <td>box.getAttribute('id');</td>
                            <td>
                                <div style="min-width: 150px;">获取属性值</div>
                            </td>
                        </tr>

                        <tr>
                            <th>2</th>
                            <td>getAttributeNames</td>
                            <td>box.getAttributeNames();</td>
                            <td>获取所有属性名的数组</td>
                        </tr>

                        <tr>
                            <th rowspan="2">3</th>
                            <td rowspan="2">getAttributeNode</td>
                            <td>box.getAttributeNode('id');</td>
                            <td>获取指定属性节点</td>
                        </tr>

                        <tr>
                            <td>box.getAttributeNode('id').nodeValue;</td>
                            <td>获取指定属性节点的属性值</td>
                        </tr>

                        <tr>
                            <th>4</th>
                            <td>attributes</td>
                            <td>box.attributes;</td>
                            <td>获取标签所有属性</td>
                        </tr>

                        <tr>
                            <th>5</th>
                            <td>hasAttribute</td>
                            <td>box.hasAttribute('id');</td>
                            <td>是否有此属性</td>
                        </tr>

                        <tr>
                            <th>6</th>
                            <td>hasAttributes</td>
                            <td>box.hasAttributes();</td>
                            <td>是否有属性</td>
                        </tr>

                        <tr>
                            <th>7</th>
                            <td>removeAttribute</td>
                            <td>box.removeAttribute('id');</td>
                            <td>删除属性</td>
                        </tr>

                        <tr>
                            <th>8</th>
                            <td>removeAttributeNode</td>
                            <td>box.removeAttributeNode(box.getAttributeNode('id'));</td>
                            <td>删除属性节点</td>
                        </tr>

                        <tr>
                            <th>9</th>
                            <td>setAttribute</td>
                            <td>box.setAttribute('id', 'box');</td>
                            <td>设置属性</td>
                        </tr>

                        <tr>
                            <th>10</th>
                            <td>setAttributeNode</td>
                            <td>box.setAttributeNode(document.createAttribute('id'));</td>
                            <td>设置属性节点，作用同上</td>
                        </tr>

                        <tr>
                            <th rowspan="3">11</th>
                            <td rowspan="3">toggleAttribute</td>
                            <td>box.toggleAttribute('id');</td>
                            <td>切换属性, 有则删并且返回false，无则加并且返回true</td>
                        </tr>

                        <tr>
                            <td>box.toggleAttribute('id', false);</td>
                            <td>只删</td>
                        </tr>

                        <tr>
                            <td>box.toggleAttribute('id', true);</td>
                            <td>只加</td>
                        </tr>

                    </table>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="domClass">dom类名的操作</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <table>
                        <colgroup>
                            <col span="2">
                            <col span="1" style="min-width:200px;">
                            <col span="1" style="min-width:200px;">
                        </colgroup>

                        <tr>
                            <td></td>
                            <th>属性/方法名</th>
                            <th>示例</th>
                            <th>说明</th>
                        </tr>

                        <tr>
                            <th rowspan="15">1</th>
                            <td rowspan="15">classList</td>
                            <td>let classlist = box.classList;</td>
                            <td>
                                <div style="min-width: 150px;">获取class集合</div>
                            </td>
                        </tr>

