`
const format = new Intl.NumberFormat('zh-Hans-CN-u-nu-hanidec');
const comment1 = new Comment('一般创建'); // 创建注释节点
const comment2 = document.createComment("假如创建一个很长的列表");
const title = document.createElement("h3"); // 创建元素
title.append(document.createTextNode("很长的列表")); // 创建文本节点

const ul = document.createElement('ul');
const fragment = document.createDocumentFragment(); // 创建容器
for(let i = 0; i < 10; i++) {
    const li = document.createElement('li');
    li.textContent = "列表" + format.format(i);
    const attr = document.createAttribute("mid");
    attr.value = i+1;
    li.setAttributeNode(attr);
    fragment.append(li);
}

ul.append(fragment);
document.body.append(comment1);
document.body.append(comment2);
document.body.append(title);
document.body.append(ul);
`,
